  </transition> -->
  <!--  -->

  <!---->
    <div v-if="iSsuccess" class="bgblacks  in"></div>
  <!--end-->
  <!--loading-->
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "goodsList",
  data: function data() {
    return {
      iSsuccess:false,
      isLoading:false,
      Data: {},//
      carData:[],//
      sendData:[],// goodslist
      shopCar:0,//
      totalPrices:0,//
      carFlag:false,// FALSE
      fishFlag:true,// TRUE
      iptBeans:0,//
      iptTable:'',//
      userHasBeans:0,//
      succeedAlertFlag:false,//
      orderId:''//orderId
    }
  },
//
  methods: {
    //
    upCarDate: function upCarDate(){
      var _this = this;
      _this.shopCar = 0;
      _this.totalPrices = 0;
      _this.carData = [];
      _this.sendData = [];
      for (var i = 0; i < _this.Data.length-1; i++) {
        var goods = _this.Data[i].goods;
        if(goods){
          for(var j = 0 ; j < goods.length ; j ++){
            //console.log(goods[j].id);
            var thisNum = goods[j].num
            if(thisNum > 0 ){
              _this.shopCar += thisNum;
              var thisCost = goods[j].sales_price;
              _this.totalPrices += thisCost * thisNum
              var obj = {
                name:goods[j].goods_name,
                id:goods[j].id,
                cost:goods[j].sales_price,
                num:goods[j].num,
                unit:goods[j].unit
              };
              var sendObj = {
                id:goods[j].id,
                count:goods[j].num
              }
              // var sendObj = [
              //   goods[j].id,
              //   goods[j].num
              // ]
              _this.carData.push(obj);
              _this.sendData.push(sendObj);
            }
          }
        }
      }
      // console.log('_this.shopCar:'+_this.shopCar);
      // console.log('_this.totalPrices'+_this.totalPrices);

       //console.log(_this.carData);
    },
    //:
    showCar: function showCar(){
      this.carFlag = !this.carFlag;
    },
    //
    goFish: function goFish(){
      this.fishFlag = !this.fishFlag;
      this.carFlag = false;
      this.succeedAlertFlag = false;
    },
    //
    clearAll: function clearAll(){
      var _this = this;
      _this.shopCar = 0;
      _this.totalPrices = 0;
      _this.carData = [];
      _this.carFlag = false;
      for(var i = 0 ; i < _this.Data.length-1 ; i ++){
        var goods = _this.Data[i].goods;
        if(goods){
          for(var j = 0 ; j < goods.length ; j ++){

              goods[j].num = 0;
              //console.log(goods[j].num);
          }
        }
      }
    },
    //Tit
    jump: function jump(theId) {
      var theCls = '.' + theId;
      theId = '#' + theId
      //console.log(theId);
      var anchor = $(theId).offset().top + $('.goodsListlistBox').scrollTop(); //id
      var sTop = $('.goodsListlistBox').scrollTop()
      //console.log('$(document).scrollTop():' + sTop);
      //console.log(anchor)
      $('.goodsTypeList').removeClass('goodsListypeAct')
      $(theCls).addClass('goodsListypeAct');
      $('.goodsListlistBox').animate({
        scrollTop: anchor + "px"
      })
    },
    //
    changeNum(pId,type){
      var _this = this;
      for(var i = 0 ; i < _this.Data.length-1 ; i ++){
        var goods = _this.Data[i].goods;
        if(goods){
          for(var j = 0 ; j < goods.length ; j ++){
            //console.log(goods[j].id);
            //console.log(goods[j].stock);
            if(pId == goods[j].id){
              var num = goods[j].num;
              if(goods[j].stock == '-'){
                if(type == 0){
                  num = num + 1;
                }else{
                  num = num - 1;
                }
              }else{
                if(num < goods[j].stock){
                  if(type == 0){
                    num = num + 1;
                  }else{
                    num = num - 1;
                  }
                }else {
                  num = num ;
                  alert('');
                }
              }
              if(num < 0){  //0
                num = 0
              }
              goods[j].num = num;
              //console.log(goods[j].num);
              this.upCarDate()
              return
            }
          }
        }
      }
    },
    //
    open(){
      var _this = this;
       this.$confirm('~', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         type: 'success'
       }).then(() => {
         this.$message({
           type: 'success',
           message: '~~'
         });
       }).catch(() => {
         this.$router.push({ name: 'orderDeta', params: { id: _this.orderId }})

       });
     },
    //
    sendOrder(){
      var _this = this;
      // console.log(1);
       //console.log(_this.sendData);
       //console.log('_this.userHasBeans:'+_this.userHasBeans);
       //console.log('_this.iptBeans:'+_this.iptBeans);
       var iptB = parseInt(_this.iptBeans);
       var uHasB = parseInt(_this.userHasBeans)
       if(iptB > uHasB){
         _this.$message({
           message: '~',
           duration: '800',
           type: 'error' // 'success'  ,  'eroor' ,  'warning'
         });
         _this.iptBeans = 0;
         return;
       }else if (_this.iptTable == '') {
         _this.$message({
           message: '',
           duration: '800',
           type: 'warning' // 'success'  ,  'eroor' ,  'warning'
         });
         return;
       }

      this.ajax(_this.port.sendOrder, {
        beans:_this.iptBeans,
        table_no:_this.iptTable,
        goodsList:_this.sendData
      }, 'POST', function(res) {
        if(res.status == 1){
          _this.userHasBeans = _this.userHasBeans - _this.iptBeans;
          _this.succeedAlertFlag = true;
          _this.orderId = res.order_id;
          _this.open();

        }else if (res.status == 0) {
          _this.$message.error(res.msg);
        }else {
          _this.$message({
            message: '~!....',
            type: 'warning'
          });
        }
        //console.log(res);
      })
    }
  },
  created: function created() {
    //do something after creating vue instance
//
    loadData: {
      var _this = this;
      // console.log(1);
      // console.log(_this.port.test);

      // 


      this.ajax(_this.port.goods, {
        shoper_id: _this.$route.params.shoper_id,
        store_id: _this.$route.params.store_id
      }, 'GET', function(res) {
        //console.log(res);
        if (res.status == 1) {
          _this.Data = res.data;
        }else{
          _this.$message({
            message: '~!....',
            type: 'warning'
          });
        }
      })

      //
      this.ajax(_this.port.getB, {
      }, 'GET', function(res) {
        //console.log(res);
        if (res.status == 1) {
          _this.userHasBeans = res.beans;
        }else{
          _this.$message({
            message: '~!....',
            type: 'warning'
          });
        }
      })
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    test: {
      //console.log('sdsds');
    }
  },
  updated: function updated() {
    //do something after updating vue instance

  }
}
</script>
<style>
.fold-enter-active {
  animation-name: fold-in;
  animation-duration: .3s;
}
.fold-leave-active {
  animation-name: fold-out;
  animation-duration: .3s;
}
@keyframes fold-in {
  0% {
    transform: translate3d(100%, 0, 0);
  }
  100% {
    transform: translate3d(50%, 0, 0);
  }
}
@keyframes fold-out {
  0% {
    transform: translate3d(50%, 0, 0);
  }
  100% {
    transform: translate3d(100%, 0, 0);
  }
}
.fold2-enter-active {
  animation-name: fold2-in;
  animation-duration: .6s;
}
.fold2-leave-active {
  animation-name: fold2-out;
  animation-duration: .3s;
}
@keyframes fold2-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fold2-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.succeedAlert{
  position: fixed;
  width: 50vw;
  min-height: 20vh;
  top: 50%;
  left: 50%;
  margin-left: -25vw;
  margin-top: -10vh;
  background-color: #fff;
  font-size: 0.30rem;
  border-radius: 0.3rem;
  z-index: 99999;
}
.succeedAlert p{
  display: flex;
  height: auto;
  justify-content: center;
  align-items: center;
  margin-top: 0.3rem;
}
.succeedAlert p img{
  width: 0.45rem
}
.operationBox{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.3rem 0.2rem 0.2rem;
}
.operationBtn{
  font-size: 0.24rem;
  color: #fff;
  background-color: #56d176;
  border-radius: 0.08rem;
  padding: 0.06rem;
}
</style>



