<template>
<div id="topUp">
  <div class="bindPhoneMain">
    <div class="bindPhoneRow">
      <span></span>
      <input disabled="disabled" type="number" class="topUpIpt" name="" v-model="userHasBeans" style="">
    </div>

    <p class="global_blkBB"></p>
    <div class="bindPhoneRow">
      <span></span>
      <input type="number" class="global_IptSad topUpIpt topUpUserNum" name="" value="" v-model="iptBeans">

    </div>
  </div>
  <div class="bind global_IptSad global_BtnCol2 global_boxSad" @click="pay()">
    
  </div>
  <div class="topUpRule">
    (1 = {{ratio}})
  </div>
  <!---->
  <div v-if="iSsuccess" class="bgblacks  in"></div>
  <!--end-->
  <!--loading-->
  <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
  <!--loading-->
</div>
</template>
<script>
export default {
  name: "topUp",
  data: function data() {
    return {
      iSsuccess: false,
      isLoading: false,
      userHasBeans: '', //
      iptBeans: '', //
      ratio: '', //
    }
  },
  methods: {
    //
    pay() {
      var _this = this
      if (!/^[1-9]\d{0,9}$/.test(this.iptBeans)) {
        this.$message({
          message: '',
          duration: '800',
          type: 'warning' // 'success'  ,  'eroor' ,  'warning'
        });
        return;
      }
      _this.ajax(_this.port.pay, {
        money: _this.iptBeans
      }, 'get', function(res) {
        //console.log(res);
        var payData = res.jsApiConfig
        //console.log('payData_________-------_-----------_-');
        //console.log(payData);
        if (res.status == 1) {
          wx.chooseWXPay({
            timestamp: payData.timeStamp, // jssdktimestamptimeStampS
            nonceStr: payData.nonceStr, //  32 
            package: payData.package, // prepay_idprepay_id=***
            signType: payData.signType, // 'SHA1''MD5'
            paySign: payData.paySign, // 
            success: function(res) {
              if (msg.errMsg == 'chooseWXPay:ok') {
                _this.ajax(_this.port.cdbPay, {
                  money: _this.iptBeans
                }, 'post', function(res) {
                  alert(res)
                  if(res.status == 1){

                    _this.$message({
                      message: '',
                      duration: '800',
                      type: 'success' // 'success'  ,  'eroor' ,  'warning'
                    });
                    _this.iptBeans = '';
                    _this.getInitData();
                  }else {
                    _this.$message({
                      message: '',
                      duration: '800',
                      type: 'error' // 'success'  ,  'eroor' ,  'warning'
                    });
                  }
                })
              } else {

                _this.$message({
                  message: '',
                  duration: '800',
                  type: 'error' // 'success'  ,  'eroor' ,  'warning'
                });
              }
              //console.log('');
              //console.log(res);
              // 
            }
          });
        }else{
          _this.$message({
            message: '....',
            duration: '800',
            type: 'error' // 'success'  ,  'eroor' ,  'warning'
          });
        }
      })
    },
    //
    getInitData() {
      var _this = this;
      this.ajax(_this.port.getB, {}, 'GET', function(res) {
        if (res.status == 1) {
          _this.userHasBeans = res.beans;
        } else {
          _this.$message({
            message: res.msg,
            duration: '800',
            type: 'error' // 'success'  ,  'eroor' ,  'warning'
          });
        }
      })
    },
    //
    getDdbBili() {
      var _this = this;
      this.ajax(_this.port.getRatio, {}, 'GET', function(res) {
        //console.log(res);
        if (res.status == 1) {
          _this.ratio = res.data;
        } else {
          _this.$message({
            message: res.msg,
            duration: '800',
            type: 'error' // 'success'  ,  'eroor' ,  'warning'
          });
        }
      })
    }
  },
  mounted: function mounted() {
    this.getInitData();
    this.getDdbBili();
  }
}
</script>



