<template>
  <div id="bindPhone">
    <div class="bindPhoneMain">
      <div class="bindPhoneRow">
        <span></span>
        <input type="number" class="global_IptSad" name="" value="" v-model="iptPhoneNumber" @change="readPhone()">
      </div>
      <p class="global_blkBB"></p>
      <div class="bindPhoneRow">
        <span></span>
        <input type="number" class="global_IptSad dCodeIpt" name="" value="" v-model="iptCode">
        <div v-show="sendDCodeFlag && PhoneT" class="spendDCode global_IptSad  global_BtnCol2" @click="sendCode()">
          
        </div>
        <div style="font-size:0.2rem" v-show="!PhoneT" class="spendDCode global_IptSad  global_BtnColRed">
          
        </div>
        <div v-show="!sendDCodeFlag" class="spendDCode global_IptSad  global_BtnCol2" >
          {{timeOut}}s
        </div>
      </div>
    </div>
      <div class="bind global_IptSad global_BtnCol2 global_boxSad" @click="sendBind()">
        
      </div>

      <!---->
        <div v-if="iSsuccess" class="bgblacks  in"></div>
      <!--end-->
      <!--loading-->
      <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
      <!--loading-->
  </div>
</template>
<script>
export default {
  name: "bindPhone",
  data: function data() {
    return {
      iSsuccess:false,
      isLoading:false,
      iptPhoneNumber:'',//
      iptCode:'',//
      sendDCodeFlag:true,//TRUE 
      timeOut:60,//
      timeInt:{},//
      PhoneT:false,//FALSE
      Code:'',//
    }
  },
  methods: {
    //
    sendCode: function sendCode() {
      var _this = this;
      _this.sendDCodeFlag = false
      var phone = _this.iptPhoneNumber;
      this.ajax(_this.port.bindPhone, {
        phone:phone,
      }, 'GET', function(res) {
        //console.log(res);
        if (res.status == 1) {
          _this.Code = res.data;
            _this.$message({
              message: '~',
              type: 'success'
            });

        }else if (res.status == 0) {
            _this.$message.error(res.msg);

        }else{
          _this.$message({message: '~!....',type: 'warning'});
        }
      })
      _this.timeMin()
    },
    //
    timeMin: function timeMin(){
      var _this = this;
      var downTime = _this.timeOut;
      _this.timeInt = setInterval(function(){
        if (_this.timeOut == 0) {
          _this.sendDCodeFlag = true;
          _this.timeOut = 60;
          clearInterval(_this.timeInt);
        }
        _this.timeOut = _this.timeOut - 1;
        //console.log(_this.timeOut);
      },1000)


    },
    //
    readPhone:function readPhone(){
      var _this = this
      var phoneNumber = _this.iptPhoneNumber
      //console.log(phoneNumber);
      if(!(/^1[34578]\d{9}$/.test(phoneNumber))){
        return false;
      }else {
        _this.PhoneT = true;
      }
    },
    //
    sendBind:function sendBind(){
      var _this = this;
      if(_this.Code == _this.iptCode){
        var phone = _this.iptPhoneNumber;
        var code = _this.iptCode;
        this.ajax(_this.port.bind, {
          phone:phone,
          code:code
        }, 'POST', function(res) {
          //console.log(res);
          if (res.status == 1) {
            _this.$router.push({ path: '/bindSucceed' })
          }else if (res.status == 0) {
              _this.$message.error(res.msg);
          }else{
            _this.$message({message: '~!....',type: 'warning'});
          }
        })
      }else {
        alert('')
      }
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance

  }
}
</script>
<style media="screen">
.msgP{
  font-size: 0.24rem;
  color: #ccc;
}
</style>



