export  default {
  install:function(Vue) {
    Vue.prototype.port={
			'addDeposit':'erp/push-all',//
			'depositInit':'erp/init-push-goods',//
      'goods':'goods/index',//
      'sendOrder':'goods/order',//
      'getRatio':'user/getconfig',//
      'getB':'user/getbeans',//
      'orderDeta':'user/orderdetail',//
      'orderList':'user/orderlist',//
      'beansCons':'user/beanscons',//
      'vipCardNum':'user/vip',//
      'topUprecord':'user/getrecharge',//
      'vipCardList':'user/viplist',//
      'bindPhone':'user/bingphone',//
      'cdbPay':'user/recharge',//
      'bind':'user/verify',//
      'configData':'wechat/config',//
      'getTeaData':'index/tea',//
      'teaDeta':'index/teadetail',//
      'pay':'wechat/pay'//
    };
    Vue.prototype.hostUrl='http://192.168.2.150:8081/';//

    Vue.prototype.ajax = function (url,data,methodType,callback) {
      var _this=this;
     //const  HostUrl=
      //const  HostUrl='http://192.168.2.174/';
      this.isLoading=true;
      this.iSsuccess=true;
      $.ajax({
        type: methodType,
        url: _this.hostUrl+url,
        data:data,
        timeout:15000,
        dataType:'json',
        success: function (data) {
          _this.isLoading=false;
          _this.iSsuccess=false;
          callback(data);
        },
        error:function (XMLHttpRequest, textStatus, errorThrown) {
          console.log(textStatus)
          if(textStatus=='timeout'){
            //alert('');
            _this.isLoading=false;
            _this.iSsuccess=false;
          }else if(textStatus=='error'){
            //alert('');
            _this.isLoading=false;
            _this.iSsuccess=false;
          }
          else if(textStatus=='parsererror'){
            //alert('');
            _this.isLoading=false;
            _this.iSsuccess=false;
          }
        }
      });
    };


    // (function () {
    //   const  HostUrl='http://192.168.2.222:8080/';
    //   $.ajax({
    //     type: 'get',
    //     url: HostUrl+'role/get-roles',
    //     data:'',
    //     dataType:'json',
    //     success: function (data) {
    //       console.log(data.data)
    //       window.localStorage.setItem('roledata',JSON.stringify(data.data));
    //     },
    //     error:function (err) {
    //       console.log(err);
    //     }
    //   });
    // })()


  }
}



