<template>
  <div id="clubCard ">
    <p class="clubCardTit pLR"></p>


    <div class="cardRow" v-for="data in Data" :key="data.key">
      <div class="cardMainBox">
        <div class="cardLeftBox">
          <p class="cardMB global_txtOver"><b>{{data.card_no}}</b></p>
          <p class="global_txtOver">{{data.shoper_name}}</p>
        </div>
        <div class="cardRightBox">
          <span class="cardCost global_txtOver">{{data.vip_total}}</span>
          <span></span>
        </div>
      </div>
      <div class="cardTimeBox">
           
      </div>
    </div>

    <div class="global_nullDiv">

    </div>


    <!---->
      <div v-if="iSsuccess" class="bgblacks  in"></div>
    <!--end-->
    <!--loading-->
    <div type="primary" element-loading-text="" v-loading.fullscreen.lock="isLoading"></div>
    <!--loading-->
</div>
</template>
<script>
export default {
  name: "clubCard",
  data: function data() {
    return {
      iSsuccess:false,
      isLoading:false,
      Data:[],
    }
  },
  mounted: function mounted() {
    //do something after mounting vue instance
    //
    loadData: {
      var _this = this;
      this.ajax(_this.port.vipCardList, {
      }, 'GET', function(res) {
        //console.log(res);
        if (res.status == 1) {
          _this.Data = res.data;
        }else if (res.status == 0) {
            _this.$message.error(res.msg);
        }else{
          _this.$message({message: '~!....',type: 'warning'});
        }
      })

    }
  }
}
</script>
<style>
</style>



